/**
 * ã‚»ãƒƒã‚·ãƒ§ãƒ³ãƒ‡ãƒ¼ã‚¿ã®å‹å®šç¾©
 */
export interface SessionData {
  token: string;
  expiresAt: number;
}

const SESSION_STORAGE_KEY = 'auth_session';

/**
 * ã‚»ãƒƒã‚·ãƒ§ãƒ³ãƒ‡ãƒ¼ã‚¿ã‚’LocalStorageã«ä¿å­˜ã™ã‚‹
 * @param sessionData - ä¿å­˜ã™ã‚‹ã‚»ãƒƒã‚·ãƒ§ãƒ³ãƒ‡ãƒ¼ã‚¿
 */
export function saveSession(sessionData: SessionData): void {
  try {
    localStorage.setItem(SESSION_STORAGE_KEY, JSON.stringify(sessionData));
  } catch (error) {
    console.error('Failed to save session:', error);
  }
}

/**
 * LocalStorageã‹ã‚‰ã‚»ãƒƒã‚·ãƒ§ãƒ³ãƒ‡ãƒ¼ã‚¿ã‚’å–å¾—ã™ã‚‹
 * @returns ã‚»ãƒƒã‚·ãƒ§ãƒ³ãƒ‡ãƒ¼ã‚¿ã€ã¾ãŸã¯å­˜åœ¨ã—ãªã„å ´åˆnull
 */
export function getSession(): SessionData | null {
  try {
    const data = localStorage.getItem(SESSION_STORAGE_KEY);
    if (!data) return null;

    const sessionData: SessionData = JSON.parse(data);

    // æœ‰åŠ¹æœŸé™ãƒã‚§ãƒƒã‚¯
    if (sessionData.expiresAt < Date.now()) {
      clearSession();
      return null;
    }

    return sessionData;
  } catch (error) {
    console.error('Failed to get session:', error);
    return null;
  }
}

/**
 * LocalStorageã‹ã‚‰ã‚»ãƒƒã‚·ãƒ§ãƒ³ãƒ‡ãƒ¼ã‚¿ã‚’å‰Šé™¤ã™ã‚‹
 */
export function clearSession(): void {
  try {
    localStorage.removeItem(SESSION_STORAGE_KEY);
  } catch (error) {
    console.error('Failed to clear session:', error);
  }
}

/**
 * ã‚»ãƒƒã‚·ãƒ§ãƒ³ãŒæœ‰åŠ¹ã‹ãƒã‚§ãƒƒã‚¯ã™ã‚‹
 * @returns ã‚»ãƒƒã‚·ãƒ§ãƒ³ãŒæœ‰åŠ¹ãªå ´åˆtrue
 */
export function isSessionValid(): boolean {
  const session = getSession();
  return session !== null;
}
