/**
 * ãƒ‘ã‚¹ãƒ¯ãƒ¼ãƒ‰ã‚’SHA-256ã§ãƒãƒƒã‚·ãƒ¥åŒ–ã™ã‚‹
 * @param password - ãƒãƒƒã‚·ãƒ¥åŒ–ã™ã‚‹ãƒ‘ã‚¹ãƒ¯ãƒ¼ãƒ‰
 * @returns ãƒãƒƒã‚·ãƒ¥åŒ–ã•ã‚ŒãŸãƒ‘ã‚¹ãƒ¯ãƒ¼ãƒ‰ï¼ˆ16é€²æ•°æ–‡å­—åˆ—ï¼‰
 */
export async function hashPassword(password: string): Promise<string> {
  const encoder = new TextEncoder();
  const data = encoder.encode(password);
  const hashBuffer = await crypto.subtle.digest('SHA-256', data);
  const hashArray = Array.from(new Uint8Array(hashBuffer));
  const hashHex = hashArray.map(b => b.toString(16).padStart(2, '0')).join('');
  return hashHex;
}

/**
 * ãƒ©ãƒ³ãƒ€ãƒ ãªã‚»ãƒƒã‚·ãƒ§ãƒ³ãƒˆãƒ¼ã‚¯ãƒ³ã‚’ç”Ÿæˆã™ã‚‹
 * @returns ãƒ©ãƒ³ãƒ€ãƒ ãªãƒˆãƒ¼ã‚¯ãƒ³æ–‡å­—åˆ—
 */
export function generateSessionToken(): string {
  const array = new Uint8Array(32);
  crypto.getRandomValues(array);
  return Array.from(array, byte => byte.toString(16).padStart(2, '0')).join('');
}

/**
 * ãƒ‘ã‚¹ãƒ¯ãƒ¼ãƒ‰ã‚’æ¤œè¨¼ã™ã‚‹
 * @param password - æ¤œè¨¼ã™ã‚‹ãƒ‘ã‚¹ãƒ¯ãƒ¼ãƒ‰
 * @param expectedHash - æœŸå¾…ã•ã‚Œã‚‹ãƒãƒƒã‚·ãƒ¥å€¤
 * @returns ãƒ‘ã‚¹ãƒ¯ãƒ¼ãƒ‰ãŒæ­£ã—ã„å ´åˆtrue
 */
export async function verifyPassword(
  password: string,
  expectedHash: string
): Promise<boolean> {
  const hash = await hashPassword(password);
  return hash === expectedHash;
}
