import React, { useRef, useEffect } from 'react';
import type { ReceivedEmotionData } from '../../types/webrtc';

interface IntensityChartProps {
  emotionData: ReceivedEmotionData[];
  userId: string;
  width?: number;
  height?: number;
}

export const IntensityChart: React.FC<IntensityChartProps> = ({
  emotionData,
  userId,
  width = 400,
  height = 200
}) => {
  const canvasRef = useRef<HTMLCanvasElement>(null);

  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    const ctx = canvas.getContext('2d');
    if (!ctx) return;

    // Canvas ã‚¯ãƒªã‚¢
    ctx.clearRect(0, 0, width, height);

    // èƒŒæ™¯
    ctx.fillStyle = '#1f2937'; // gray-800
    ctx.fillRect(0, 0, width, height);

    // ã‚°ãƒªãƒƒãƒ‰æç”»
    ctx.strokeStyle = '#374151'; // gray-700
    ctx.lineWidth = 1;

    // æ°´å¹³ã‚°ãƒªãƒƒãƒ‰ (intensity 0 - 8000)
    for (let i = 0; i <= 4; i++) {
      const y = (height - 40) * (1 - i / 4) + 20;
      ctx.beginPath();
      ctx.moveTo(40, y);
      ctx.lineTo(width - 20, y);
      ctx.stroke();

      // ãƒ©ãƒ™ãƒ«
      ctx.fillStyle = '#9ca3af'; // gray-400
      ctx.font = '12px sans-serif';
      ctx.textAlign = 'right';
      ctx.fillText((i * 20).toString(), 35, y + 5); // 0, 20, 40, 60, 80, 100
    }

    // ãƒ‡ãƒ¼ã‚¿ãŒãªã„å ´åˆ
    if (emotionData.length === 0) {
      ctx.fillStyle = '#6b7280'; // gray-500
      ctx.font = '14px sans-serif';
      ctx.textAlign = 'center';
      ctx.fillText('æ„Ÿæƒ…ãƒ‡ãƒ¼ã‚¿ã‚’å¾…æ©Ÿä¸­...', width / 2, height / 2);
      return;
    }

    // æœ€æ–°300ä»¶ã®ãƒ‡ãƒ¼ã‚¿ã‚’å–å¾—
    const maxDataPoints = 300;
    const filteredData = emotionData.slice(-maxDataPoints);

    if (filteredData.length < 2) {
      ctx.fillStyle = '#6b7280';
      ctx.font = '14px sans-serif';
      ctx.textAlign = 'center';
      ctx.fillText('ãƒ‡ãƒ¼ã‚¿è“„ç©ä¸­...', width / 2, height / 2);
      return;
    }
    console.log('length of filtered data: ', filteredData.length)
    console.log('length of emotion data : ', emotionData)
    // æŠ˜ã‚Œç·šã‚°ãƒ©ãƒ•ã‚’æç”»
    ctx.strokeStyle = '#3b82f6'; // blue-500
    ctx.lineWidth = 2;
    ctx.beginPath();

    filteredData.forEach((data, index) => {
      const x = 40 + (index / (filteredData.length - 1)) * (width - 60);
      const y = 20 + (1 - (Math.min(data.intensity, 100) / 100)) * (height - 40); // 0-8000ã®ç¯„å›²ã§æ­£è¦åŒ–

      if (index === 0) {
        ctx.moveTo(x, y);
      } else {
        ctx.lineTo(x, y);
      }
    });

    // æŠ˜ã‚Œç·šã‚’æç”»
    ctx.strokeStyle = '#3b82f6';
    ctx.stroke();

    // ãƒ‡ãƒ¼ã‚¿ãƒã‚¤ãƒ³ãƒˆã‚’æç”»
    filteredData.forEach((data, index) => {
      const x = 40 + (index / (filteredData.length - 1)) * (width - 60);
      const y = 20 + (1 - (Math.min(data.intensity, 100) / 100)) * (height - 40);

      ctx.fillStyle = getIntensityColor(data.intensity);
      ctx.beginPath();
      ctx.arc(x, y, 2, 0, 2 * Math.PI);
      ctx.fill();
    });

    // ç¾åœ¨ã®å€¤ã‚’è¡¨ç¤º
    const latestData = filteredData[filteredData.length - 1];
    if (latestData) {
      ctx.fillStyle = '#ffffff';
      ctx.font = 'bold 16px sans-serif';
      ctx.textAlign = 'left';
      ctx.fillText(
        `${userId}: ${Math.round(latestData.intensity)}/100`,
        50,
        30
      );

      // ç¬‘ã„ãƒ¬ãƒ™ãƒ«è¡¨ç¤º
      ctx.fillStyle = getLaughLevelColor(latestData.laughLevel);
      ctx.font = '12px sans-serif';
      ctx.fillText(
        latestData.laughLevel.toUpperCase(),
        50,
        50
      );
    }

  }, [emotionData, userId, width, height]);

  return (
    <div className="bg-gray-800 rounded-lg p-4">
      <h3 className="text-sm font-medium text-white mb-2">
        {userId} ã®æ„Ÿæƒ…å¼·åº¦ (ãƒªã‚¢ãƒ«ã‚¿ã‚¤ãƒ )
      </h3>
      <canvas
        ref={canvasRef}
        width={width}
        height={height}
        className="border border-gray-600 rounded"
      />
      {emotionData.length > 0 && (
        <div className="mt-2 text-xs text-gray-400">
          ãƒ‡ãƒ¼ã‚¿æ•°: {emotionData.length}ä»¶ |
          æœ€æ–°: {new Date(emotionData[emotionData.length - 1]?.timestamp).toLocaleTimeString()}
        </div>
      )}
    </div>
  );
};

// å¼·åº¦ã«å¿œã˜ãŸè‰²ã‚’å–å¾—
function getIntensityColor(intensity: number): string {
  if (intensity > 70) return '#10b981'; // green-500 (é«˜å¼·åº¦)
  if (intensity > 40) return '#f59e0b'; // yellow-500 (ä¸­å¼·åº¦)
  return '#6b7280'; // gray-500 (ä½å¼·åº¦)
}

// ç¬‘ã„ãƒ¬ãƒ™ãƒ«ã«å¿œã˜ãŸè‰²ã‚’å–å¾—
function getLaughLevelColor(laughLevel: string): string {
  switch (laughLevel) {
    case 'high': return '#10b981'; // green-500
    case 'medium': return '#f59e0b'; // yellow-500
    case 'low': return '#6b7280'; // gray-500
    default: return '#6b7280';
  }
}