import { createContext, useContext, useState, useEffect } from 'react';
import type { ReactNode } from 'react';
import { authConfig } from '../config/auth.config';
import { verifyPassword, generateSessionToken } from '../utils/auth';
import { saveSession, getSession, clearSession } from '../utils/storage';

/**
 * èªè¨¼çŠ¶æ…‹ã®å‹å®šç¾©
 */
interface AuthState {
  authenticated: boolean;
  loading: boolean;
  error: string | null;
}

/**
 * èªè¨¼ã‚³ãƒ³ãƒ†ã‚­ã‚¹ãƒˆã®å‹å®šç¾©
 */
interface AuthContextType extends AuthState {
  login: (password: string) => Promise<boolean>;
  logout: () => void;
  checkAuth: () => boolean;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

/**
 * AuthProviderã®Props
 */
interface AuthProviderProps {
  children: ReactNode;
}

/**
 * èªè¨¼ãƒ—ãƒ­ãƒã‚¤ãƒ€ãƒ¼ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆ
 */
export function AuthProvider({ children }: AuthProviderProps) {
  const [authState, setAuthState] = useState<AuthState>({
    authenticated: false,
    loading: true,
    error: null,
  });

  // åˆæœŸåŒ–æ™‚ã«ã‚»ãƒƒã‚·ãƒ§ãƒ³ã‚’ãƒã‚§ãƒƒã‚¯
  useEffect(() => {
    const initAuth = () => {
      // èªè¨¼ãŒç„¡åŠ¹ãªå ´åˆã¯å¸¸ã«èªè¨¼æ¸ˆã¿çŠ¶æ…‹
      if (!authConfig.enabled) {
        setAuthState({
          authenticated: true,
          loading: false,
          error: null,
        });
        return;
      }

      // ã‚»ãƒƒã‚·ãƒ§ãƒ³ã®æœ‰åŠ¹æ€§ã‚’ãƒã‚§ãƒƒã‚¯
      const session = getSession();
      setAuthState({
        authenticated: session !== null,
        loading: false,
        error: null,
      });
    };

    initAuth();
  }, []);

  /**
   * ãƒ­ã‚°ã‚¤ãƒ³å‡¦ç†
   */
  const login = async (password: string): Promise<boolean> => {
    setAuthState(prev => ({ ...prev, loading: true, error: null }));

    try {
      // ãƒ‘ã‚¹ãƒ¯ãƒ¼ãƒ‰æ¤œè¨¼
      const isValid = await verifyPassword(password, authConfig.passwordHash);

      if (!isValid) {
        setAuthState({
          authenticated: false,
          loading: false,
          error: 'ãƒ‘ã‚¹ãƒ¯ãƒ¼ãƒ‰ãŒæ­£ã—ãã‚ã‚Šã¾ã›ã‚“',
        });
        return false;
      }

      // ã‚»ãƒƒã‚·ãƒ§ãƒ³ãƒˆãƒ¼ã‚¯ãƒ³ç”Ÿæˆ
      const token = generateSessionToken();
      const expiresAt = Date.now() + authConfig.sessionDurationMs;

      // ã‚»ãƒƒã‚·ãƒ§ãƒ³ä¿å­˜
      saveSession({ token, expiresAt });

      setAuthState({
        authenticated: true,
        loading: false,
        error: null,
      });

      return true;
    } catch (error) {
      console.error('Login error:', error);
      setAuthState({
        authenticated: false,
        loading: false,
        error: 'ãƒ­ã‚°ã‚¤ãƒ³ä¸­ã«ã‚¨ãƒ©ãƒ¼ãŒç™ºç”Ÿã—ã¾ã—ãŸ',
      });
      return false;
    }
  };

  /**
   * ãƒ­ã‚°ã‚¢ã‚¦ãƒˆå‡¦ç†
   */
  const logout = () => {
    clearSession();
    setAuthState({
      authenticated: false,
      loading: false,
      error: null,
    });
  };

  /**
   * èªè¨¼çŠ¶æ…‹ã‚’ãƒã‚§ãƒƒã‚¯
   */
  const checkAuth = (): boolean => {
    if (!authConfig.enabled) {
      return true;
    }

    const session = getSession();
    const isAuth = session !== null;

    // çŠ¶æ…‹ãŒå¤‰ã‚ã£ãŸå ´åˆã¯æ›´æ–°
    if (isAuth !== authState.authenticated) {
      setAuthState(prev => ({
        ...prev,
        authenticated: isAuth,
      }));
    }

    return isAuth;
  };

  const contextValue: AuthContextType = {
    ...authState,
    login,
    logout,
    checkAuth,
  };

  return (
    <AuthContext.Provider value={contextValue}>
      {children}
    </AuthContext.Provider>
  );
}

/**
 * èªè¨¼ã‚³ãƒ³ãƒ†ã‚­ã‚¹ãƒˆã‚’ä½¿ç”¨ã™ã‚‹ã‚«ã‚¹ã‚¿ãƒ ãƒ•ãƒƒã‚¯
 */
export function useAuth(): AuthContextType {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
}
